//================================================== IMPORTS ==================================================

import React from 'react';
import ReactDOM from 'react-dom';
import Moment from 'react-moment';
import 'moment-timezone';

import './scripts/default.css';

//import CharData from './scripts/characters.json'



//================================================== FUNCTIONS ==================================================

function handleErrors(response) {
	if (!response.ok) {
		throw Error(response.statusText);
	}
	return response;
}



//================================================== CLASSES ==================================================

//-------------------------------------------------- CLASS: NameContainer --------------------------------------------------

class NameContainer extends React.Component {

	render() {

		return (
			<div className="charName" id="charNameID" onChange={this.props.func}>
				{this.props.charName}
			</div>
		)
	}
}


//-------------------------------------------------- CLASS: FilmList --------------------------------------------------

class FilmList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			filmList: [],
			filmListLoaded: false,
		};
	}


	componentDidUpdate(prevProps, prevState, snapshot) {

		if (prevProps !== this.props) {

			fetch(this.props.charURL)

				.then(handleErrors)

				.then(response => response.json())
				.then(json => {
					this.setState({
						filmList: json,
						filmListLoaded: true,
					})
				})
				.catch((error) => {
					console.log('FilmList: componentDidUpdate(prevProps, prevState, snapshot): SOMETHING WENT WRONG!!!' + error)
					this.setState({
						filmList: [],
						filmListLoaded: false,
					})
				})
		}

		return null;
	}


	render() {

		var { filmListLoaded, filmList } = this.state;

		if(!filmListLoaded) {
			return (
				<div className="filmInfo" id="filmInfoID">
					<NameContainer className="nameContainer" charName={this.props.charName} charURL={this.props.charURL} func={this.updateInfo} />
				</div>
			)
		} else {
			return (
				<div className="filmInfo" id="filmInfoID">

					<NameContainer className="nameContainer" charName={this.props.charName} charURL={this.props.charURL} func={this.updateInfo} />

					{filmList.films.map((film, index) => (
						<FilmContainer className="filmContainer" key={index} filmURL={filmList.films[index]} />
					))}
				</div>
			)
		}
	}
}


//-------------------------------------------------- CLASS: FilmContainer --------------------------------------------------

class FilmContainer extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			filmDate: "release date",

			items: [],
			isLoaded: false,
		};
	}


	componentDidMount() {

		fetch(this.props.filmURL)
			.then(response => response.json())
			.then(json => {
				this.setState({
					isLoaded: true,
					items: json,
				})
			})
	}


	render() {

		var { isLoaded, items } = this.state;

		if(!isLoaded) {
			return <div className="filmDate">FilmContainer: Loading...</div>;
		} else {
			return (
				<div className="filmContainer">
					<div className="filmTitle" id="filmTitleID">
						{items.title}
					</div>

					<div className="filmDate" id="filmDateID">
						<Moment className="filmDate" id="filmDateID" date={items.release_date} format="dddd, MMMM D YYYY" />
					</div>

					<div className="filmInfo" id="filmInfoID">
						{items.opening_crawl}
					</div>
				</div> 
			)
		}
	}
}



//-------------------------------------------------- CLASS: BodyContainer --------------------------------------------------

class BodyContainer extends React.Component {

	constructor(props) {
		super(props);

		this.updateInfo = this.updateInfo.bind(this);

		this.state = {
			charName: "Click/tap	 a character on the left to get names and movie listings.",
			charURL: null,

			charList: [],
			charsLoaded: false,

			filmList: [],
			filmListLoaded: false,

			filmURL: "https://swapi.co/api/films/2/",
		};
	}


	componentDidMount() {

		fetch('./scripts/characters.json')
			.then(response => response.json())
			.then(json => {
				this.setState({
					charList: json,
					charsLoaded: true,
				})
			});
	}


	updateInfo = (charDetail) => {

		this.setState({
			charName: charDetail.name,
			charURL: charDetail.url,
		});
	}


	render() {

		var charArray = ["luke", "vader", "obiwan", "r2d2"];

		var { charsLoaded, charList } = this.state;

		if(!charsLoaded) {
			return <div>Loading...</div>;
		} else {

			var charKeys = Object.keys(charList.characters);

			return (

				<div className="bodyContainer" id="bodyContainerID">

					<div className="charContainer" id="charContainerID">
						{charList.characters.map((charDetail, index) => {
							return <div className="charThumb" id={charArray[index]} key={charKeys[index]} onClick={() => this.updateInfo(charDetail)}></div>
						})}
					</div>

					<div className="infoContainer" id="infoContainerID">

						<FilmList className="filmContainer" charName={this.state.charName} charURL={this.state.charURL} filmList={this.state.filmList} filmListLoaded={this.state.filmListLoaded} filmURL={this.state.filmURL} func={this.updateInfo} />

					</div> 

				</div> 
			)
		}
	}
}



//================================================== RENDER ==================================================

ReactDOM.render(
	<BodyContainer />,
	document.getElementById('bodyContainerID')
);



